@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&family=Roboto:wght@400;500;700&display=swap');
body{
  font-family: 'Noto Sans KR', sans-serif;
  font-family: 'Roboto', sans-serif;
  color: #222;
  
}
header{
  width: 100%;
  height: 54px;
  position: fixed;
  z-index: 9;
 .df{
   position: relative;
   display: flex;
   box-sizing: border-box;
   box-sizing: border-box;
   padding: 0px 0px 0px 48px;
   justify-content: space-between;
   z-index: 9;
   h1 a{
      background: url(../public/img/Logo.png)0px 50%/100% auto no-repeat;
      display: block;
      width: 120px;      
      height: 54px;      
      text-indent: -9999px;         
   }
   .centerMenuItem{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
   }
   ul{     
     display: block;               
     z-index: 9;     
     li{
       float: left; 
       padding-right: 24px;
       z-index: 9;
       a{
         line-height: 54px;
         z-index: 12;
         font-weight: 500;
       }
     }
     li.pr0{
       padding-right: 0px;
     }
   }   
 }
 .cursorFollow{
   position: absolute;
   line-height: 2rem;
   background: rgba(0,0,0,0.1);        
   color: rgba(0,0,0,0.1);        
   border-radius: 10px;      
   z-index: -1;
   padding: 0px 8px !important;   
   transition: all 0.3s ease;
   top: 50%;
   transform: translate(-8px,-50%);
 } 
 .cursorFollow.on{
   opacity: 1;
   transition: all 0.1s linear;
 }
 .cursorFollow.off{
  opacity: 0;
  transition: all 0.1s linear;
}
}
/*page common*/
.page{
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%,50%;
  width: 100%;
  height: 100vh;
  position: relative;
}
.btns{
  position: absolute;
  bottom: 114px;
  left: 50%;
  transform: translateX(-50%);
  a{
    padding: 12px 0px;
    display: block;
    float: left;
    text-align: center;
    width: 256px;
    margin: 0px 12px;
    border-radius: 20px;
    font-size: 0.8rem;    
  }
  a.left{
    background: rgba(23, 26, 32, 0.8);
    color: #FFF;
  }
  a.right{
    background: rgba(255,255,255,0.8);
    color: #222;
  }
}
.top{
  position: absolute;
  left: 50%;
  top: 15%;
  transform: translate(-50%,0%);
  h2{
    text-align: center;
    font-size: 2.5rem;
  }
  a{
    display: inline-block;
    position: relative;
  }
  a span{
    position: absolute;
    display: block;
    width: 100%;
    background: #000;
    height: 1px;
    bottom: -1px;
    transition: all 0.5s;
  }
  a:hover span{
    position: absolute;
    display: block;
    width: 100%;
    background: #000;
    height: 2px;
    bottom: -2px;
    transition: all 0.5s;
  }
}
